import React from 'react';
import {Overlay, Tooltip, Popover} from 'react-bootstrap';
import {translation} from '../resources/translations'

export default class Login extends React.Component {

  constructor(props){
    super();
    this.id = "LoginForm";
    this.modeChange = props.controller.modeChange;
    this.errorMsg = props.error ? props.error.message : '';

    this.state = {username: '',
                  password: ''}
  }

  render() {

      this.loginError = this.props.error && this.props.error.type === "login";
      this.passwordError = this.props.error && this.props.error.type === "password";

      const controller = this.props.controller;
      const loginTooltip = (
          <Tooltip id="tooltip">asd</Tooltip>
      );
      const passwordTooltip = (
          <Tooltip id="tooltip"><strong>Holy guacamole!</strong> Check this info.</Tooltip>
      );

      const loginParams = this.props.loginParams;


      return(
          <div className="login-box">
            <form id="credentials" action="/cas/login?service=https://localhost:8070/raamit" method="post">
              <input type="hidden" name="lt" value={loginParams.loginTicket} />
              <input type="hidden" name="execution" value={loginParams.executionKey} />
              <input type="hidden" name="_eventId" value="submit" />

              <div>
                  <input id="username" name="username" value={this.state.username} type="text"
                         ref="login"
                         className={this.loginError ? "invalid-input error" : "login-input"}
                         placeholder={translation("login.usernamePlaceholder")}
                         onChange={e => this.setState({username: e.target.value})}/>
                  <Overlay
                      show={this.loginError}
                      target={() => this.refs.login}
                      placement="right"
                      container={this.refs.login}
                    >
                      <Popover id="popover-contained">
                          {this.errorMsg}
                      </Popover>
                  </Overlay>
              </div>


              <div>

                  <input id="password" name="password"
                         type="password"
                         ref="password"
                         className={this.passwordError ? "invalid-input error" : "login-input"}
                         placeholder={translation("login.passwordPlaceholder")}
                         onChange={e => this.setState({password: e.target.value})}/>
                  <Overlay
                      show={this.passwordError}
                      target={() => this.refs.password}
                      placement="right"
                      container={this.refs.password}
                  >
                      <Popover id="popover-contained">
                          {this.errorMsg}
                      </Popover>
                  </Overlay>
              </div>
              <div ref="pwreset" className="link" onClick={this.modeChange}>

                  {translation("login.forgotPassword")}

              </div>

              <input name="submit" type="submit" className="btn btn-login" value={translation("login.button")} />
             </form>
          </div>
      )
  }
}




